import { ElementRef, Injectable, NgZone } from '@angular/core';

import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/auditTime';

import { DOMService } from './dom.service';

import { FRAME_DURATION, isDocumentAvailable } from '../util';

interface EventArgs {
    target: HTMLElement;
}

/**
 * @hidden
 */
@Injectable()
export class ScrollableService {
    private element: ElementRef;
    private subscription: Subscription;

    constructor(private _dom: DOMService, private _zone: NgZone) { }

    public forElement(element: ElementRef): ScrollableService {
        this.unsubscribe();

        this.element = element;

        return this;
    }

    public subscribe(callback: Function): void {
        if (!callback || !isDocumentAvailable() || !this.element) { return; }

        const nativeElement = this._dom.nativeElement(this.element);
        const parents: Array<HTMLElement> = this._dom.scrollableParents(this.element);

        this._zone.runOutsideAngular(() => {
            const observables = parents.map(p => Observable.fromEvent(p, "scroll").auditTime(FRAME_DURATION));
            const subscriber = (e: EventArgs) => (
                this._zone.run(() => (
                    callback(this.isVisible(nativeElement, e.target))
                ))
            );

            this.subscription = Observable.merge(...observables).subscribe(subscriber);
        });
    }

    public unsubscribe(): void {
        if (!this.subscription) { return; }

        this.subscription.unsubscribe();
    }

    public isVisible(elem: any, container: any): boolean {
        const elemRect = this._dom.boundingOffset(elem);
        const containerRect = this._dom.boundingOffset(container);

        if (elemRect.bottom < containerRect.top) { return false; }

        if (elemRect.bottom > containerRect.bottom) { return false; }

        if (elemRect.right > containerRect.right) { return false; }

        if (elemRect.left < containerRect.left) { return false; }

        return true;
    }
}



